<template>
  <div class="container">
    <h1>{{events[this.id].title}}</h1>
    <p v-html="events[this.id].description"></p>
  </div>
</template>

<script>
import json from '../data/events.json'
import navigationMixin from '../mixins/navigationMixin.js'
import setColorsMixin from '../mixins/setCollectionColorsMixin.js'
export default {
  data() {
    return {
      events: json,
      id: this.$route.params.id
    }
  },
  mixins: [setColorsMixin, navigationMixin],
  mounted() {
    this.toNavBottom2();
    this.setColors('black');
    this.setMobileNavColors(false);
  },
  watch: {
    $route (to, from) {
      this.id = this.$route.params.id;
    }
  }
}
</script>
