<template>
  <div class="container">

      <div class="editorial-text">
        <h1>{{editorials[this.id].title}}</h1>
        <p>{{editorials[this.id].description}}</p>
      </div>
      <div class="images" v-for="image in editorials[this.id].images">
        <img class="editorial-image" :src="image.url" :alt="image.alt">
      </div>
    <div class="bg"></div>
  </div>

</template>

<script>
  import json from '../data/editorials.json'
  import navigationMixin from '../mixins/navigationMixin.js'
  import setColorsMixin from '../mixins/setCollectionColorsMixin.js'
  export default {
    data() {
      return {
        id: this.$route.params.id,
        editorials: json
      }
    },
    mixins: [setColorsMixin, navigationMixin],
    mounted() {
      this.toNavBottom1();
      this.setColors('black');
      this.setMobileNavColors(false);
      this.init();
    },
    methods: {
      init() {
        $('.editorial-text, .editorial-image').css('opacity', '1');
        $('.editorial-text, .editorial-image').css('top', '0');

      },
      leave() {
      $('.editorial-text, .editorial-image').css('opacity', '0');
      $('.editorial-text, .editorial-image').css('top', '20px');
    }
  },
    watch: {
      $route (to, from) {
        this.init();
        this.id = this.$route.params.id;
      }
    },
    beforeRouteLeave(to, from, next) {
      this.leave();
      setTimeout(function () {
        next();
      }, 300);
    },
    beforeRouteUpdate(to, from, next) {
      this.leave();
      setTimeout(function () {
        next();
      }, 300);
    }
  }
</script>

<style scoped>

  .images {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .editorial-image {
    width: 50%;
    margin: 20px;
    opacity: 0;
    top: 20px;
    transition: opacity .3s ease, top .3s ease;
    /* animation: fade-in .5s ease;
    animation-fill-mode: forwards; */
    position: relative;
  }

  .editorial-text {
    margin-left: 40px;
    margin-top: 100px;
    opacity: 0;
    top: 20px;
    transition: opacity .3s ease, top .3s ease;
    /* animation: fade-in .5s ease;
    animation-fill-mode: forwards; */
    position: relative;
  }

  .enter {
    opacity: 1;
    top: 0px;
  }

  .leave {
    opacity: 0;
    top: 20px;
  }

  .editorial-text h2 {
    font-size: 2.5em;
    margin-bottom: 20px;
  }

  .editorial-text p {
    font-size: 1.2em;
  }

  @keyframes fade-in {
    from {
      opacity: 0;
      top: 20px;
    } to {
      opacity: 1;
      top: 0px;
    }
  }

  .bg {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    background-image: url('/collections/ipseity/bg.jpg');
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
  }
</style>
